"use client";
import { useEffect, useState } from "react";

export default function AllTasksPage() {
  const [tasks, setTasks] = useState([]);
  const [message, setMessage] = useState("");

  const token = localStorage.getItem("token");

  // Fetch tasks
  const fetchTasks = async () => {
    try {
      const res = await fetch("http://localhost:5000/api/alltasks", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (!res.ok) {
        setMessage("Failed to load tasks");
        return;
      }

      const data = await res.json();
      setTasks(data);
    } catch (err) {
      console.error(err);
      setMessage("Cannot reach backend!");
    }
  };

  // Delete task
  const handleDelete = async (id) => {
    try {
      const res = await fetch(`http://localhost:5000/api/alltasks/${id}`, {
        method: "DELETE",
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (res.ok) {
        setTasks(tasks.filter((task) => task._id !== id));
      } else {
        setMessage("Failed to delete task");
      }
    } catch (err) {
      console.error(err);
      setMessage("Cannot reach backend!");
    }
  };

  useEffect(() => {
    fetchTasks();
  }, []);

  return (
    <div className="min-h-screen bg-sky-200 p-6">
      {/* App name + icon */}
      <div className="flex items-center gap-2 mb-6">
        <img src="/favicon.ico" alt="App Icon" className="w-8 h-8" />
        <h1 className="text-xl font-bold">Task Manager</h1>
      </div>

      <h2 className="text-2xl font-bold text-center mb-6">All Tasks</h2>

      {message && <p className="text-center text-red-500">{message}</p>}

      <div className="flex flex-col gap-4">
        {tasks.length > 0 ? (
          tasks.map((task) => (
            <div
              key={task._id}
              className="bg-white p-4 rounded shadow-md flex flex-col gap-2"
            >
              <h3 className="font-bold text-lg">{task.title}</h3>
              <p className="text-gray-600">{task.description}</p>
              <p
                className={`text-sm ${
                  task.pending ? "text-yellow-600" : "text-green-600"
                }`}
              >
                {task.pending ? "Pending" : "Completed"}
              </p>
              <div className="flex gap-2 mt-2">
                <button
                  onClick={() =>
                    (window.location.href = `/edit/${task._id}`)
                  }
                  className="bg-blue-600 text-white px-3 py-1 rounded hover:bg-blue-700"
                >
                  Edit
                </button>
                <button
                  onClick={() => handleDelete(task._id)}
                  className="bg-red-600 text-white px-3 py-1 rounded hover:bg-red-700"
                >
                  Delete
                </button>
              </div>
            </div>
          ))
        ) : (
          <p className="text-center">No tasks found</p>
        )}
      </div>

      <div className="flex justify-center mt-6">
        <button
          onClick={() => (window.location.href = "/create")}
          className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
        >
          + Create Task
        </button>
      </div>
    </div>
  );
}
